/* .Toggle */
.toggle {
  width: 80px;
  height: 30px;
  background: #8e24aa;
  margin: 20px auto;
  position: relative;
  border-radius: 50px;
  box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.5), 0px 1px 0px rgba(255, 255, 255, 0.2); }

.toggle:after {
  content: '';
  position: absolute;
  top: 14px;
  left: 14px;
  height: 2px;
  width: 52px;
  background: #111;
  border-radius: 50px;
  box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.5), 0px 1px 0px rgba(255, 255, 255, 0.2); }

.toggle label {
  display: block;
  width: 22px;
  height: 22px;
  cursor: pointer;
  position: absolute;
  top: 4px;
  z-index: 1;
  left: 4px;
  background: #fcfff4;
  border-radius: 50px;
  transition: all 0.4s ease;
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.3);
  background: linear-gradient(top, #fcfff4 0%, #dfe5d7 40%, #b3bead 100%); }

.toggle label:after {
  content: '';
  width: 10px;
  height: 10px;
  position: absolute;
  top: 6px;
  left: 6px;
  background: #333;
  border-radius: 50px;
  box-shadow: inset 0px 1px 1px black, 0px 1px 0px rgba(255, 255, 255, 0.9); }

.toggle input[type=checkbox] {
  visibility: hidden; }

.toggle input[type=checkbox]:checked + label {
  left: 54px; }

.toggle input[type=checkbox]:checked + label:after {
  background: #7a12b5;
  /*activeColor*/ }

/* end  */
.dot-loader,
.dot-loader:before,
.dot-loader:after {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation: load7 1.8s infinite ease-in-out;
  animation: load7 1.8s infinite ease-in-out; }

.dot-loader {
  color: #9c27b0;
  font-size: 8px;
  margin: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 999;
  text-indent: -9999em;
  transform: translateZ(0);
  animation-delay: -0.16s; }

.dot-loader:before,
.dot-loader:after {
  content: '';
  position: absolute;
  top: 0; }

.dot-loader:before {
  left: -3.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s; }

.dot-loader:after {
  left: 3.5em; }

@-webkit-keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em; }
  40% {
    box-shadow: 0 2.5em 0 0; } }

@keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em; }
  40% {
    box-shadow: 0 2.5em 0 0; } }

.add-button {
  color: #fff;
  background-color: #9c27b0;
  right: 16px;
  bottom: 16px;
  position: fixed;
  transform: none;
  transition: transform 225ms cubic-bezier(0.4, 0, 0.2, 1) 195ms;
  width: 56px;
  height: 56px;
  padding: 0;
  font-size: 14px;
  min-width: 0;
  box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 6px 10px 0px rgba(0, 0, 0, 0.14), 0px 1px 18px 0px rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  min-height: 36px;
  font-family: "heebo";
  font-weight: 400;
  line-height: 1.75;
  border-radius: 50%;
  letter-spacing: 0.02857em;
  text-transform: uppercase;
  border: 0;
  cursor: pointer;
  margin: 0;
  display: inline-flex;
  outline: 0;
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;
  justify-content: center;
  text-decoration: none;
  vertical-align: middle;
  align-items: center;
  user-select: none;
  border: 0;
  cursor: pointer;
  margin: 0;
  display: inline-flex;
  outline: none;
  z-index: 9999; }

.add-button:active {
  outline: none;
  box-shadow: 0px 7px 8px -4px rgba(0, 0, 0, 0.2), 0px 12px 17px 2px rgba(0, 0, 0, 0.14), 0px 5px 22px 4px rgba(0, 0, 0, 0.12); }

.add-button:hover {
