.content {
    padding: 0 !important;

    .__Weather {
        font-size: 100%;
        font-family: "heebo";
        background-size: cover;
        -webkit-background-size: cover;
        -moz-background-size: cover;
        -o-background-size: cover;
        -ms-background-size: cover;
        background-attachment: fixed;

        /* start editing from here */
        a {
            text-decoration: none;
        }

        .txt-rt {
            text-align: right;
        }

        /* text align right */
        .txt-lt {
            text-align: left;
        }

        /* text align left */
        .txt-center {
            text-align: center;
        }

        /* text align center */
        .float-rt {
            float: right;
        }

        /* float right */
        .float-lt {
            float: left;
        }

        /* float left */
        .clear {
            clear: both;
        }

        /* clear float */
        .pos-relative {
            position: relative;
        }

        /* Position Relative */
        .pos-absolute {
            position: absolute;
        }

        /* Position Absolute */
        .vertical-base {
            vertical-align: baseline;
        }

        /* vertical align baseline */
        .vertical-top {
            vertical-align: top;
        }

        /* vertical align top */
        nav.vertical ul li {
            display: block;
        }

        /* vertical menu */
        nav.horizontal ul li {
            display: inline-block;
        }

        /* horizontal menu */
        img {
            max-width: 100%;
        }

        h1,
        h2,
        h3,
        h4,
        h5,
        h6,
        input,
        p,
        a,
        select {
            font-family: "heebo";
            margin: 0;
        }

        ul,
        label {
            margin: 0;
            padding: 0;
        }

        body a:hover,
        body a {
            text-decoration: none;
        }

        /*-- main --*/
        .agileits_copyright {
            text-align: center;
        }

        .agileits_copyright p {
            font-size: 1em;
            color: #fff;
            line-height: 1.8em;
            padding: 0 1em;
            text-transform: capitalize;
            margin-top: 2em;
        }

        .agileits_copyright p a {
            color: #4bba9f;
            text-decoration: underline;
        }

        .agileits_copyright p a:hover {
            color: #fff;
        }

        input[type="submit"],
        a {
            transition: .5s ease-in;
            -webkit-transition: .5s ease-in;
            -moz-transition: .5s ease-in;
            -o-transition: .5s ease-in;
            -ms-transition: .5s ease-in;
        }

        .main {
            padding: 3em 0;
        }

        .main h1 {
            text-align: center;
            color: rgb(212, 209, 209);
            font-size: 2.8em;
            margin: 0em 0 1em;
            text-transform: uppercase;
            letter-spacing: 5px;
            font-weight: 700;
        }

        /*-- //main --*/
        .w3_agile_main_grids {
            // margin: 0 auto;
            background: url(../../assets/images/1.jpg) no-repeat 0px 0px;
            background-size: cover;
            background-repeat: round;
            -webkit-background-size: cover;
            -moz-background-size: cover;
            -o-background-size: cover;
            -ms-background-size: cover;
        }

        .w3layouts_main_grid {
            padding: 1.5em 2em;
            background: rgba(13, 5, 88, 0.26);
        }

        .w3layouts_main_grid_left {
            float: left;
            width: 50%;
        }

        .w3layouts_main_grid_left h2 {
            font-size: 1.5em;
            color: rgb(235, 231, 231);
            font-weight: 700;
        }

        .w3layouts_main_grid_left p {
            font-size: 14px;
            color: #999;
            margin: .5em 0 0;
        }

        .w3layouts_main_grid_left h3 {
            font-size: 1em;
            color: rgb(223, 219, 219);
            font-weight: bold;
            margin: 3em 0 .5em;
        }

        .w3layouts_main_grid_left h4 {
            font-size: 3.5em;
            color: #fff;
            font-weight: bold;
        }

        .w3layouts_main_grid_left h4 span {
            font-size: .5em;
            vertical-align: top;
        }

        .w3layouts_main_grid_right {
            float: right;
            width: 50%;
            text-align: center;
            margin-top: 2em;
        }

        .agile_main_grid_left {
            background: rgba(16, 99, 74, 0.9);
        }

        .wthree_main_grid_left_grid {
            padding: 0 1.5em;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
        }

        .agileinfo_main_grid_right_grid {
            padding: 1.5em 0;
        }

        .w3_agileits_main_grid_right {
            background: rgba(119, 12, 12, 0.9);
        }

        .w3l_main_grid_left_grid1_left {
            float: left;
            width: 50%;
        }

        .w3l_main_grid_left_grid1_left h3 {
            text-transform: uppercase;
            font-size: .9em;
            font-weight: 600;
            color: #d1caca;
            letter-spacing: 1px;
        }

        .w3l_main_grid_left_grid1_left p {
            font-size: 2em;
            color: #74cab0;
            font-weight: 600;
        }

        .w3l_main_grid_left_grid1_left p span {
            font-size: .5em;
        }

        .w3l_main_grid_left_grid1_right {
            float: right;
            width: 50%;
            text-align: right;
        }

        .w3ls_main_grid_left_grid1 {
            margin: 1.5em 0;
            width: 50%;

        }

        .w3_main_grid_right_grid1_right {
            width: 33.33%;
            text-align: center;
        }
        .w3_main_grid_right_grid1_left p,
        .w3_main_grid_right_grid1_right p {
            font-size: 1em;
            color: rgb(223, 219, 219);
            font-weight: 600;
        }


        .w3_main_grid_right_grid1_right p i {
            font-style: normal;
        }

        .w3_main_grid_right_grid1_right p span {
            display: block;
            font-size: .8em;
        }

        .w3_main_grid_right_grid1 {
            margin-bottom: 2em;
            display: flex;
            justify-content: space-evenly;
        }

        .w3_main_grid_right_grid1:last-child {
            margin: 0;
        }

        #w3time {
            font-size: 2em;
            margin: 1em 0 .5em;
            color: #4bba9f;
            font-weight: 600;
            text-align: right;
        }

        .w3layouts_date_year {
            font-size: 1.1em;
            color: #fff;
            text-align: right;
        }

        /*-- start-responsive-design --*/

        @media (max-width:1280px) {
            .w3l_main_grid_left_grid1_right {
                width: 22%;
            }

            .w3l_main_grid_left_grid1_left {
                width: 78%;
            }
        }


        @media (max-width: 900px) {
            .main h1 {
                font-size: 2.5em;
                letter-spacing: 3px;
            }

            .w3layouts_main_grid_left h2 {
                font-size: 1.3em;
            }
        }



        @media (max-width: 768px) {

            .w3_main_grid_right_grid1_left p,
            .w3_main_grid_right_grid1_right p {
                font-size: .9em;
            }

            .w3_main_grid_right_grid1_left {
                text-align: left;
            }

            #w3time {
                margin: .5em 0;
            }

            .wthree_main_grid_left_grid {
                padding: 0 1.5em 0;
            }
        }



        @media (max-width: 667px) {
            .w3layouts_main_grid {
                padding: 1.5em 2em;
            }

            .w3layouts_main_grid_left h2 {
                font-size: 1.2em;
            }

            .w3layouts_main_grid_left h3 {
                margin: 2em 0 .5em;
            }

            .w3layouts_main_grid_left h4 {
                font-size: 3em;
            }

            .main h1 {
                font-size: 2.3em;
            }
        }

        @media (max-width: 640px) {
            .w3layouts_date_year {
                font-size: 1em;
            }

            .w3layouts_main_grid_left h2 {
                font-size: 1.1em;
            }
        }

        @media (max-width: 600px) {
            .w3l_main_grid_left_grid1_left h3 {
                letter-spacing: 0px;
            }

            .main h1 {
                font-size: 2.1em;
            }
        }

        @media (max-width: 568px) {
            canvas#sleet {
                width: 50px;
                height: 50px;
            }

            .w3layouts_main_grid_left h2 {
                font-size: 1em;
            }

            .w3layouts_date_year {
                font-size: .9em;
            }

            .agile_main_grid_left {
                float: none;
                width: 100%;
            }

            .wthree_main_grid_left_grid {
                padding: 2em;
            }

            .w3_agileits_main_grid_right {
                float: none;
                width: 100%;
            }

            .agileits_copyright p {
                font-size: 14px;
            }
        }

        @media (max-width: 480px) {
            .main h1 {
                font-size: 1.8em;
                letter-spacing: 2px;
            }

            .main {
                padding: 2em 0;
            }

            .w3layouts_main_grid {
                padding: 1.5em;
            }

            .w3layouts_main_grid_left h2 {
                font-size: .85em;
            }

            #w3time {
                font-size: 1.8em;
            }

            .w3layouts_date_year {
                font-size: .8em;
            }

            .w3layouts_main_grid_right {
                margin-top: 1em;
            }

            .w3l_main_grid_left_grid1_right canvas {
                width: 30px;
                height: 30px;
            }

            .w3l_main_grid_left_grid1_left p {
                font-size: 1.7em;
            }

            .w3l_main_grid_left_grid1_left h3 {
                font-size: .8em;
            }

            .wthree_main_grid_left_grid {
                padding: 2em;
            }
        }

        @media (max-width: 414px) {
            .main h1 {
                font-size: 1.6em;
            }
        }

        @media (max-width: 384px) {
            .main h1 {
                letter-spacing: 1px;
            }
        }

        @media (max-width: 375px) {
            .main h1 {
                font-size: 1.5em;
            }

            #w3time {
                font-size: 1.6em;
                margin: 0 0 .3em;
            }
        }

        @media (max-width: 320px) {
            .main h1 {
                font-size: 1.3em;
            }

            .w3layouts_main_grid_left h4 {
                font-size: 2.5em;
            }

            .w3layouts_main_grid_left h3,
            .w3layouts_main_grid_left h2 {
                font-size: .8em;
            }

            .w3layouts_main_grid_left p,
            .w3_main_grid_right_grid1_left p,
            .w3_main_grid_right_grid1_right p,
            .agileits_copyright p {
                font-size: 13px;
            }

            .w3_main_grid_right_grid1 {
                margin-bottom: 0.5em;
            }

            canvas#sleet {
                width: 40px;
                height: 40px;
            }

            #w3time {
                font-size: 1.5em;
            }

            .w3layouts_main_grid {
                padding: 1.5em 1.5em 1em;
            }

            .wthree_main_grid_left_grid {
                padding: 1.5em;
            }
        }





        ul.resp-tabs-list,
        p {
            margin: 0px;
            padding: 0px;
        }

        .resp-tabs-list li {
            color: #000000;
            font-size: 1em;
            display: inline-block;
            padding: 0 35px 13px;
            margin: 0 0 2em;
            list-style: none;
            cursor: pointer;
            float: left;
            border-bottom: 2px solid;
            border-color: #911b11;
            background: none !important;
            font-weight: 600;
            width: 50%;
            text-align: center;
        }

        .resp-tabs-container {
            padding: 0px;
            background-color: transparent;
            clear: left;
        }

        h2.resp-accordion {
            cursor: pointer;
            padding: 5px;
            display: none;
        }

        .resp-tab-content {
            display: none;
            padding: 0;
        }

        .resp-tab-active {
            color: #fff !important;
            border-color: #18e9a7 !important;
            border: none;
        }

        .resp-content-active,
        .resp-accordion-active {
            display: block;
        }

        .resp-tab-content {
            border: none;
        }

        h2.resp-accordion {
            font-size: 1em;
            font-weight: 600;
            color: #212121;
            border: 1px solid #c1c1c1;
            margin: 1em 0 0;
            padding: 10px 15px;
        }

        h2.resp-tab-active {
            margin-bottom: 1em !important;
        }

        h2.resp-tab-title:last-child {
            border-bottom: 12px solid #c1c1c1 !important;
            background: blue;
        }

        /*-----------Vertical tabs-----------*/
        .resp-vtabs ul.resp-tabs-list {
            float: left;
            width: 30%;
        }

        .resp-vtabs .resp-tabs-list li {
            display: block;
            padding: 15px 15px !important;
            margin: 0 0 4px;
            cursor: pointer;
            float: none;
        }

        .resp-vtabs .resp-tabs-container {
            padding: 0px;
            background-color: #fff;
            border: 1px solid #c1c1c1;
            float: left;
            width: 68%;
            min-height: 250px;
            border-radius: 4px;
            clear: none;
        }

        .resp-vtabs .resp-tab-content {
            border: none;
            word-wrap: break-word;
        }

        .resp-vtabs li.resp-tab-active {
            position: relative;
            z-index: 1;
            margin-right: -1px !important;
            padding: 14px 15px 15px 14px !important;
            border-top: 1px solid;
            border: 1px solid #5AB1D0 !important;
            border-left: 4px solid #5AB1D0 !important;
            margin-bottom: 4px !important;
            border-right: 1px #FFF solid !important;
        }

        .resp-arrow {
            width: 0;
            height: 0;
            float: right;
            margin-top: 3px;
            border-left: 6px solid transparent;
            border-right: 6px solid transparent;
            border-top: 12px solid #c1c1c1;
        }

        h2.resp-tab-active span.resp-arrow {
            border: none;
            border-left: 6px solid transparent;
            border-right: 6px solid transparent;
            border-bottom: 12px solid #ffffff;
        }

        /*-----------Accordion styles-----------*/
        h2.resp-tab-active {
            background: none !important;
        }

        .resp-easy-accordion h2.resp-accordion {
            display: block;
        }

        .resp-easy-accordion .resp-tab-content {
            border: 1px solid #c1c1c1;
        }

        .resp-easy-accordion .resp-tab-content:last-child {
            border-bottom: 1px solid #c1c1c1;
            /* !important;*/
        }

        .resp-jfit {
            width: 100%;
            margin: 0px;
        }

        .resp-tab-content-active {
            display: block;
        }

        h2.resp-accordion:first-child {
            border-top: 1px solid #c1c1c1;
            /* !important;*/
            margin-top: 0 !important;
        }

        /*Here your can change the breakpoint to set the accordion, when screen resolution changed*/
        @media only screen and (max-width: 900px) {
            .resp-tabs-list li {
                padding: 0 30px 13px;
            }
        }

        @media only screen and (max-width: 768px) {
            ul.resp-tabs-list {
                display: none;
            }

            h2.resp-accordion {
                display: block;
            }

            .resp-vtabs .resp-tab-content {
                border: 1px solid #C1C1C1;
            }

            .resp-vtabs .resp-tabs-container {
                border: none;
                float: none;
                width: 100%;
                min-height: 100px;
                clear: none;
            }

            .resp-accordion-closed {
                display: none !important;
            }

            .resp-vtabs .resp-tab-content:last-child {
                border-bottom: 1px solid #c1c1c1 !important;
            }
        }

        @media only screen and (max-width: 320px) {
            h2.resp-accordion {
                font-size: .9em;
            }
        }
    }
}